<template>
    <div class="backData">
        <div class="inpuWrap">
            <Card class="base-info">
                <p slot="title">数据录入</p>
                <Form :model="baseInfo" :label-width="80">
                    <FormItem class="one-four" label="订单号">
                        <Input v-model="baseInfo.orderNum" placeholder="请输入订单号"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="分组">
                        <Select v-model="doorStyleIndex" @on-change="changeStyle">
                            <Option v-for="(item, index) in groupList" :value="index" :key="index">{{ item.title }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="款式">
                        <Select v-model="doorTypeIndex">
                            <Option v-for="(item, index) in doorTypeList" :value="index" :key="index">{{ item.title }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="套线型号">
                        <Input v-model="baseInfo.lineType" placeholder="请输入套线型号"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="拼接角度" v-if="isEFlag">
                        <Select v-model="baseInfo.angle">
                            <Option v-for="(item, index) in angleList" :value="item" :key="index">{{ item }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="竖套线厚度" v-if="isEFlag">
                        <InputNumber v-model="baseInfo.verLineTick" placeholder="请输入竖套线厚度"></InputNumber>
                    </FormItem>
                    <FormItem class="one-four" label="横套线厚度" v-if="isEFlag">
                        <InputNumber v-model="baseInfo.acrossLineTick" placeholder="请输入横套线厚度"></InputNumber>
                    </FormItem>
                    <FormItem class="one-four" label="材质类型">
                        <Select v-model="baseInfo.materialType">
                            <Option v-for="item in materialList" :value="item" :key="item">{{ item }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="材质">
                        <Input v-model="baseInfo.material" placeholder="请输入材质"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="颜色">
                        <Input v-model="baseInfo.color" placeholder="请输入颜色"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="脚墩高度">
                        <InputNumber v-model="baseInfo.footPierHeight" placeholder="请输入脚墩高度"></InputNumber>
                    </FormItem>
                    <FormItem class="half" label="客户地址">
                        <Input v-model="baseInfo.address" placeholder="请输入客户地址"></Input>
                    </FormItem>
                </Form>
            </Card>
            <my-input-table @addNew="addNew"></my-input-table>
            <Card style="margin-top:30px;">
                <p slot="title">
                    录入表格
                </p>
                <Table :columns="columns" :data="outDataList"></Table>
            </Card>
            <div class="next-wrap">
                <Button type="primary" @click="showData">生成数据</Button>
            </div>
        </div>
        <div class="img-wrap" id="bigCanvas"></div>
        <div class="img-wrap" id="smallCanvas"></div>
        <output-data v-if="doorTypeList[doorTypeIndex]" 
            ref="outputData"></output-data>
    </div>
</template>

<script>
import MyInputTable from '@/components/MyInputTable.vue'
import OutputData from '@/components/OutputData.vue'
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
let FileSaver = require('file-saver');
const outLogo = require('@/assets/outLogo.png')
export default {
    components: {
        MyInputTable,
        OutputData
    },
    data() {
        return {
            downloadFlag: false,
            showPicture: false,
            groupList: [],
            // bigImg: null,
            // smallImg: null,
            materialList: ['贴皮', '混油'],
            angleList: [90, 45],
            outLogo: null,
            // 门款式
            doorStyleIndex: 0,
            doorTypeIndex: 0,
            baseInfo: {
                // 订单号
                orderNum: '',
                // 套线型号
                lineType: '',
                // 门款式
                doorStyle: '',
                // 门型
                doorType: '',
                // E套线拼接角度
                angle: 90,
                // 竖套线厚度
                verLineTick: '',
                // 横套线厚度
                acrossLineTick: '',
                // 材质，混油还是铁皮,0混油, 1贴皮
                materialType: '混油',
                // 材质名称
                material: '',
                // 颜色
                color: '',
                // 客户地址
                address: '',
                // 脚墩高度
                footPierHeight: null
            },
            columns: [{
                title: '序号',
                key: 'index',
                width: 60,
                align: 'center',
                render: (h, params) => {
                    return h('InputNumber', {
                        props:{
                            type:'text',
                            value:this.outDataList[params.index].index
                        },
                        style: {
                            width: '40px'
                        },
                        on:{
                            'on-change':(event) => {
                                this.outDataList[params.index].index = event;
                            }
                        },
                    })
                }
            }, {
                title: '位置及名称',
                key: 'name'
            }, {
                title: '门洞尺寸(长*宽*厚)',
                render: (h, params) => {
                    let data = params.row
                    let info = this.getHWTInfo(data.doorHeight, data.doorWidth, data.doorThick)
                    return h('div', info)
                }
            }, {
                title: '门扇尺寸(长*宽*厚)',
                render: (h, params) => {
                    let data = params.row
                    let info = this.getHWTInfo(data.doorLeafHeight, data.doorLeafWidth, this.algorithm.baseInfo.doorLeafThick)
                    if(data.name !== '房门连套') info = ''
                    return h('div', info)
                }
            },  {
                title: '外到顶高度',
                key: 'outToTop'
            }, {
                title: '内到顶高度',
                key: 'innerToTop'
            },{
                title: '脚墩数量',
                key: 'footPier'
            },
            {
                title: '操作',
                render: (h, params) => {
                    return h('div', [
                        h('Button', {
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            style: {
                                marginRight: '10px'
                            },
                            on: {
                                click: () => {
                                    this.outDataList.splice(params.index, 1)
                                    // this.show(params.index)
                                }
                            }
                        }, '删除'),
                    ])
                    
                },
            }],
            outDataList: [],
            groupMap: {},
            bigList: [],
            smallList: [],
            bigCanvas: null,
            smallCanvas: null
        }
    },
    computed: {
        isEFlag() {
            return this.baseInfo.lineType.substring(0,1) === 'E'
        },
        algorithm() {
            let list = this.doorTypeList
            if(list.length > 0) {
                console.log(this.doorTypeIndex);
                return list[this.doorTypeIndex].algorithm
            }
        },
        doorTypeList() {
            if(this.groupList.length > 0) {
                return this.groupList[this.doorStyleIndex].itemList
            }else {
                return []
            }
        },
        bigImg() {
            if(this.algorithm) {
                return this.algorithm.baseInfo.bigImg
            }else {
                return ''
            }
            
        },
        smallImg() {
            if(this.algorithm) {
                return this.algorithm.baseInfo.smallImg
            }else {
                return ''
            }
            
        },
        username() {
            return common.username
        }
    },
    mounted() {
        this.getGroup()
        this.outLogo = require('@/assets/outLogo.png')
    },
    methods: {
        // 获取分组
        getGroup() {
            common.get('classes/Group').then(res => {
                let arr = []
                res.results.forEach(ele => {
                    arr.push(ele.objectId)
                    ele.itemList = []
                    this.groupMap[ele.objectId] = ele
                })
                this.getItemList(arr)
            })
        },
        // 获取ItemList
        getItemList(list) {
            let where = {
                groupId :{
                    $in: list
                }
            }
            let info = '?where=' + JSON.stringify(where)
            common.get('classes/itemList' + info).then(res => {
                res.results.forEach(ele => {
                    this.groupMap[ele.groupId].itemList.push(ele)
                })
                for(let i in this.groupMap) {
                    let item = this.groupMap[i]
                    this.groupList.push(item)
                }
            })
        },
        // 获取canvas
        getCanvas(id) {
            let isBig = id === 'bigCanvas'
            let dom = document.getElementById(id)
            let _this = this
            let img = new Image()
            let baseInfo = this.baseInfo
            img.setAttribute('crossOrigin', 'Anonymous');
            img.src = this.bigImg +'?' + (new Date().getTime())
            let logo = new Image()
            logo.src = _this.outLogo
            img.onload = () => {
                html2canvas(dom, {
                    width: 4000,
                    height: 3000,
                }).then(function(canvas) {
                    // console.log(canvas);
                    let ctx = canvas.getContext('2d')
                    ctx.fillStyle = "#000";
                    // 高度固定2900
                    let height = 2900
                    let textHeight = 1300
                    let textLeft = 3000
                    let firstHeight = 0
                    ctx.lineWidth="5";
                    ctx.rect(0,0,4500,3375);
                    ctx.rect(150,150,4200,3030);
                    ctx.stroke();
                    ctx.font="60px Arial";
                    ctx.drawImage(img, 160, 160, (img.width * height) / img.height, height)
                    // console.log(img);
                    // console.log((img.width * height) / img.height, '========img.width============');
                    // console.log(height, '========img.height============');
                    ctx.drawImage(logo, 300, 3195, 680, 155.5)
                    ctx.fillText('制图人:' + common.username, 3800, 3305);
                    _this.drawText('订单号:' +  baseInfo.orderNum, 150 ,firstHeight, 800,ctx )
                    _this.drawText('地址:' + baseInfo.address, 1100 ,firstHeight, 1800,ctx )
                    _this.drawText('颜色:' +baseInfo.material + baseInfo.color, 3000 ,firstHeight, 1200,ctx )
                    ctx.fillText( '房门尺寸:', textLeft, textHeight);
                    let listName = isBig ? 'bigList' : 'smallList'
                    _this[listName].forEach(ele =>{
                        textHeight += 120
                        ctx.fillText(ele.index + '#   ' + ele.info, textLeft, textHeight);
                    })
                    let name = isBig ? '-big' : '-small'
                    // ctx.translate(3000,3000);
                    // ctx.rotate(Math.PI / 2);//旋转
                    // console.log(ctx);
                    // ctx.rotate(20*Math.PI/180);
                    // document.body.appendChild(canvas)
                    _this.savePDF(canvas, _this.baseInfo.orderNum + name)
                    // canvas.toBlob(function(blob) {
                    //     let name = isBig ? '-big' : '-small'
                    //     saveAs(blob, _this.baseInfo.orderNum + name + '.png');
                    // });
                });
            }
        },
        savePDF(canvas, name) {  
            var contentWidth = canvas.width;
            var contentHeight = canvas.height;
            // var pageHeight = contentWidth / 592.28 * 841.89;    
            // var leftHeight = contentHeight;
            //页面偏移
            //a4纸的尺寸[595.28,841.89]，html页面生成的canvas在pdf中图片的宽高
            var imgWidth = 595.28;
            var imgHeight =  (contentWidth / contentHeight) * 595.28;
            var pageData = canvas.toDataURL('image/jpeg', 1.0);
            var pdf = new jsPDF('', 'pt', 'a4');
            let pageHeight = 841.89
            //放大会清晰一点
            pdf.internal.scaleFactor = 1.33;
            console.log(imgHeight);
            console.log((pageHeight - imgHeight) / 2, '=======(pageHeight - imgHeight) / 2=========');
            pdf.addImage(pageData, 'JPEG', imgWidth, 225, imgHeight, imgWidth, name, 'NONE', 90);
            //有两个高度需要区分，一个是html页面的实际高度，和生成pdf的页面高度(841.89)
            //当内容未超过pdf一页显示的范围，无需分页
            // if (leftHeight < pageHeight) {
                
            // } else {
            //     while (leftHeight > 0) {
            //         pdf.addImage(pageData, 'JPEG', 0, position + 40, imgWidth, imgHeight)
            //         leftHeight -= pageHeight;
            //         position -=  841.89;
            //         //避免添加空白页
            //         if (leftHeight > 0) {
            //             pdf.addPage();
            //         }
            //     }
            // }
            pdf.save(name + '.pdf');
        },
        drawText(t,x,y,w,context ){
            var chr = t.split("");
            var temp = "";				
            var row = [];
            context.font = "60px Arial";
            context.fillStyle = "#000";
            context.textBaseline = "middle";
            for(var a = 0; a < chr.length; a++){
                if( context.measureText(temp).width < w ){
                    ;
                }
                else{
                    row.push(temp);
                    temp = "";
                }
                temp += chr[a];
            }
            row.push(temp);
            let index = 0
            for(var b = 0; b < row.length; b++){
                if(b === 1) {
                    index += 100
                }else {
                    index = index + 60
                }
                context.fillText(row[b],x,y+(b+1)*20 + index);
            }
        },
        showData() {
            this.getList();
            this.$Message.loading({
                content: '开始下载文件...',
                duration: 3
            })
            this.getCanvas('bigCanvas')
            this.getCanvas('smallCanvas')
            // 生成数据
            // this.$refs.outputData.initData(this.baseInfo, this.doorTypeList[this.doorTypeIndex].algorithm, this.outDataList)
        },
        changeStyle(index) {
            this.doorTypeIndex = 0
        },
        // 添加一个新的项目
        addNew(item) {
            let aInfo = this.algorithm.baseInfo
            let len = this.outDataList.length
            item.doorLeafHeight = item.doorHeight + aInfo.doorLeafHeight
            item.doorLeafWidth = item.doorWidth + aInfo.doorLeafWidth
            if(len === 0) {
                item.index = 1
            }else {
                item.index = this.outDataList[len - 1].index + 1
            }
            this.outDataList.push(item)
        },
        getList() {
            this.bigList = []
            this.smallList = []
            this.outDataList.forEach(item => {
                if(item.name === '房门连套') {
                    let info = item.doorLeafHeight + '*' + item.doorLeafWidth + '*' + this.algorithm.baseInfo.doorLeafThick + '=1'
                    if(item.doorLeafWidth >= 750) {
                        this.bigList.push({
                            index: item.index,
                            info: info
                        })
                    }else {
                        this.smallList.push({
                            index: item.index,
                            info: info
                        })
                    }
                }
            })
        },
        getHWTInfo(h, w, t, n) {
            let info = h + '*' + w + '*' + t
            if(n) {
                info += '=' + n
            }
            return info
        },
    },
}
</script>

<style lang="scss">
@import 'src/assets/common.scss';
// .canvas-modal .ivu-modal{
//     width: 1080px !important;
// }
.backData {
    .inpuWrap {
        padding: 60px 820px 60px 10px;
        .base-info {
            .ivu-card-head {
                height: 53px;
            }
            .steps-container {
                position: absolute;
                width: 60%;
                left: 20%;
                margin-left: 10%;
            }
        }
        .next-wrap {
            margin-top: 30px;
            text-align:center;
            button {
                font-size: 14px;
                padding: 5px 40px 6px;
                &:first-child {
                    margin-right:10px;
                }
            }
        }
    }  
}
</style>
